.Services {
    h1 {
        border: #5E18E5 solid 5px;
        border-radius: 8px;
        width: 115px;
        background-color: #5E18E5;
        text-align: center;
        font-size: 18px;
        font-weight: 400;
    }
    ul {
        padding-left: 70px;
    }
    li {
        padding: 10px;
        position: relative;
        right: 20px;
    }
}

@media screen and (max-width: 500px) {
    .Services {
        li {
            font-size: 14px;
        }
    }
 }